*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}
h2{
  font-size: 1em;
  max-width: 200px;

}
h3{
  font-size: 0.7em;
}
ul {
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
}
p {
  max-width: 200px;
  font-size: 0.5em;
  max-height: 200px;
  overflow: hidden;
}
.card {
  padding: 12px;
}
.card img{
max-width: 200px;
max-height: 200px;
border-radius: 15px;
}